import React, {useEffect, useRef} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {generateApiKey} from "../../../redux/thunks/generateApiKey";
import {toast} from "react-toastify";
import Modal from 'react-modal';
import {copyToClipboard} from "../../../utils/copy";
import useTranslation from "../../../hooks/useTranslation";

const {useState} = require( "react" );

const GenerateApiKeys = ({count, refreshApiKeys}) => {

    Modal.setAppElement('#acpt-admin-app');

    // manage global state
    const dispatch = useDispatch();
    const {fetched, success, loading, errors} = useSelector(state => state.generateApiKeyReducer);

    // manage local state
    const didMountRef = useRef(false);
    const [modalIsOpen, setIsOpen] = useState(false);
    const [copyText, setCopyText] = useState(useTranslation("Copy"));

    // modal
    const openModal = () => {
        setIsOpen(true);
    };

    const afterOpenModal = () => {};

    const closeModal = () => {
        setIsOpen(false);
    };

    const handleGenerateApiKey = () => {
        dispatch( generateApiKey() );
    };

    // handle form submission outcome
    useEffect(() => {
        if (didMountRef.current){
            if(!loading){
                if(success){
                    refreshApiKeys();
                    setCopyText("Copy");
                    openModal();
                }

                if(errors.length > 0){
                    errors.map((error) => {
                        toast.error(error);
                    });
                }
            }
        } else {
            didMountRef.current = true;
        }
    }, [loading]);

    return (
        <React.Fragment>
            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                className="acpt-modal text-center"
                contentLabel="Example Modal"
            >
                <h2 className="title">
                    {useTranslation("Your new generated API key")}
                </h2>
                <p>
                    {useTranslation("This is the only time that the secret access key can be viewed or copied. You cannot recover it later. However, you can delete and create or regenerate new API key at any time.")}
                </p>
                <div className="api-key">
                    {fetched.key}-{fetched.secret}
                </div>
                <div className="acpt-buttons">
                    <button
                        className="acpt-btn acpt-btn-primary"
                        onClick={e => {
                            e.preventDefault();
                            e.stopPropagation();
                            copyToClipboard(`${fetched.key}-${fetched.secret}`);
                            setCopyText(useTranslation("Copied"));
                        }}
                    >
                        {copyText}
                    </button>
                    <button
                        className="acpt-btn acpt-btn-primary-o"
                        onClick={closeModal}
                    >
                        {useTranslation("Close")}
                    </button>
                </div>
            </Modal>
            <a
                className="acpt-btn acpt-btn-primary"
                href="#"
                onClick={e => {
                    e.preventDefault();
                    e.stopPropagation();
                    handleGenerateApiKey();
                }}
            >
                {count > 0 ? useTranslation('Regenerate') : useTranslation('Generate')} API key
            </a>
        </React.Fragment>
    );
};

export default GenerateApiKeys;