import React from 'react';
import "swagger-ui-react/swagger-ui.css";
import ApiKeysListElement from "./ApiKeysListElement";
import Pagination from "../../reusable/Pagination";
import useTranslation from "../../../hooks/useTranslation";

const ApiKeysList = ({count, elements, page, perPage, refreshApiKeys}) => {

    const totalPages = Math.ceil( count / perPage );

    if(elements.length === 0){
        return (
            <div className="acpt-alert acpt-alert-secondary">
                {useTranslation('No API keys found.')}
            </div>
        );
    }

    return (
        <div className="acpt-card">
            <div className="acpt-card__header borderless">
                <div className="acpt-card__inner">
                    <h3>{useTranslation('Your API keys')}</h3>
                    {count} {useTranslation('record(s)')} {useTranslation('found')}
                </div>
            </div>
            <div className="acpt-card__body">
                <div className="acpt-table-responsive">
                    <table className={`acpt-table ${document.globals.is_rtl === true ? `rtl` : ``}`}>
                        <thead>
                            <tr>
                                <th>{useTranslation('Key')}</th>
                                <th>{useTranslation('Secret')}</th>
                                <th>{useTranslation('Generated at')}</th>
                                <th>{useTranslation('Actions')}</th>
                            </tr>
                        </thead>
                        <tbody>
                            {elements.map((element)=> <ApiKeysListElement refreshApiKeys={refreshApiKeys} element={element} />)}
                        </tbody>
                    </table>
                </div>
            </div>
            {totalPages > 1 && (
                <div className="acpt-card__footer" style={{border: "none"}}>
                    <div className="acpt-card__inner">
                        <Pagination currentPage={page ? page : 1} perPage={perPage} records={count}/>
                    </div>
                </div>
            )}
        </div>
    );
};

export default ApiKeysList;