import {wpAjaxRequest} from "../../utils/ajax";
import {deleteApiKeyFailure, deleteApiKeyInProgress, deleteApiKeySuccess} from "../actions/deleteApiKeyActions";

export const deleteApiKey = (id) => async (dispatch, getState) => {
    try {
        dispatch(deleteApiKeyInProgress());
        const res = await wpAjaxRequest("deleteApiKeyAction", {id:id});
        (res.success === true) ? dispatch(deleteApiKeySuccess()) : dispatch(deleteApiKeyFailure(res.error)) ;
    } catch (e) {
        console.log(e);
        dispatch(deleteApiKeyFailure(e));
    }
};