import {wpAjaxRequest} from "../../utils/ajax";
import {fetchApiKeysFailure, fetchApiKeysInProgress, fetchApiKeysSuccess} from "../actions/fetchApiKeysActions";

export const fetchApiKeys = (meta) => async (dispatch, getState) => {
    try {
        dispatch(fetchApiKeysInProgress(meta));
        const fetched = await wpAjaxRequest('fetchApiKeysAction', meta ? meta : {} );
        dispatch(fetchApiKeysSuccess(fetched));
    } catch ( e ) {
        dispatch(fetchApiKeysFailure(e));
    }
};
