import React, {useEffect, useRef} from 'react';
import "swagger-ui-react/swagger-ui.css";
import Modal from 'react-modal';
import {deleteApiKey} from "../../../redux/thunks/deleteApiKey";
import {useDispatch, useSelector} from "react-redux";
import {toast} from "react-toastify";
import useTranslation from "../../../hooks/useTranslation";

const {useState} = require( "react" );

const ApiKeysListElement = ({refreshApiKeys, element}) => {

    Modal.setAppElement('#acpt-admin-app');

    // manage global state
    const dispatch = useDispatch();
    const {errors, success, loading} = useSelector(state => state.deleteApiKeyReducer);

    // manage local state
    const didMountRef = useRef(false);
    const [modalIsOpen, setIsOpen] = useState(false);

    // modal
    const openModal = () => {
        setIsOpen(true);
    };

    const afterOpenModal = () => {};

    const closeModal = () => {
        setIsOpen(false);
    };

    // handle form submission outcome
    useEffect(() => {
        if (didMountRef.current){
            if(!loading){
                if(success){
                    refreshApiKeys();
                    toast.success(useTranslation("The API key was successfully deleted"));
                    closeModal();
                }

                if(errors.length > 0){
                    errors.map((error) => {
                        toast.error(error);
                    });
                }
            }
        } else {
            didMountRef.current = true;
        }
    }, [loading]);

    return (
        <tr>
            <td>
                {element.key}
            </td>
            <td>
                {element.secret}
            </td>
            <td>
                {element.createdAt}
            </td>
            <td>
                <Modal
                    isOpen={modalIsOpen}
                    onAfterOpen={afterOpenModal}
                    onRequestClose={closeModal}
                    className="acpt-modal text-center"
                    contentLabel="Delete API key"
                >
                    <h2 className="title">
                        {useTranslation("Delete API key")}
                    </h2>
                    <p>
                        {useTranslation("You are going to delete this API key. Are you sure?")}
                    </p>
                    <div className="acpt-buttons">
                        <button
                            className="acpt-btn acpt-btn-danger"
                            onClick={e => {
                                e.preventDefault();
                                e.stopPropagation();
                                dispatch( deleteApiKey(element.id) );
                            }}
                        >
                            {useTranslation("Yes, delete it")}
                        </button>
                        <button
                            className="acpt-btn acpt-btn-primary-o"
                            onClick={closeModal}
                        >
                            {useTranslation("Close")}
                        </button>
                    </div>
                </Modal>
                <a
                    href="#"
                    onClick={e => {
                        e.preventDefault();
                        e.stopPropagation();
                        openModal();
                    }}
                >
                    {useTranslation("Delete")}
                </a>
            </td>
        </tr>
    );
};

export default ApiKeysListElement;