import {wpAjaxRequest} from "../../utils/ajax";
import {
    fetchApiKeysCountFailure,
    fetchApiKeysCountInProgress,
    fetchApiKeysCountSuccess
} from "../actions/fetchApiKeysCountActions";

export const fetchApiKeysCount = () => async (dispatch, getState) => {
    try {
        dispatch(fetchApiKeysCountInProgress());
        const fetched = await wpAjaxRequest('fetchApiKeysCountAction' );
        dispatch(fetchApiKeysCountSuccess(fetched));
    } catch ( e ) {
        dispatch(fetchApiKeysCountFailure(e));
    }
};