import React, {useEffect, useRef, useState} from 'react';
import Breadcrumbs from "../../reusable/Layout/Breadcrumbs";
import {changeCurrentAdminMenuLink, metaTitle} from "../../../utils/misc";
import SwaggerUI from "swagger-ui-react";
import "swagger-ui-react/swagger-ui.css";
import GenerateApiKeys from "./GenerateApiKeys";
import ApiKeysList from "./ApiKeysList";
import {useParams} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {filterByLabel} from "../../../utils/objects";
import {fetchApiKeys} from "../../../redux/thunks/fetchApiKeys";
import Spinner from "../../reusable/Loader/Spinner";
import {fetchApiKeysCount} from "../../../redux/thunks/fetchApiKeysCount";
import Layout from "../../reusable/Layout";
import ActionsBar from "../../reusable/Layout/ActionsBar";
import useTranslation from "../../../hooks/useTranslation";

const ApiDashboard = () => {

    // manage global state
    const dispatch = useDispatch();
    const {fetched, loading} = useSelector(state => state.fetchApiKeysReducer);
    const {fetched: fetchedCount, loading:loadingCount} = useSelector(state => state.fetchApiKeysCountReducer);
    const {loading: settingsLoading, fetched: settings} = useSelector(state => state.fetchSettingsReducer);

    // manage local state
    const {page} = useParams();
    const didMountRef = useRef(false);
    const [fetchedSuccess, setFetchedSuccess] = useState(null);
    const perPage = (settings.length > 0 && filterByLabel(settings, 'key', 'records_per_page') !== '') ? filterByLabel(settings, 'key', 'records_per_page').value : 20;

    useEffect(() => {
        metaTitle(useTranslation("APIs dashboard panel"));
        changeCurrentAdminMenuLink('#/api');
        dispatch(fetchApiKeysCount());
        dispatch(fetchApiKeys({
            page: page ? page : 1,
            perPage: perPage
        }));
    }, [page]);

    // handle fetch outcome
    useEffect(() => {
        if (didMountRef.current){
            if(!loading && !settingsLoading){
                setFetchedSuccess(true);
            }
        } else {
            didMountRef.current = true;
        }
    }, [loading]);

    // refresh records
    const handleRefreshApiKeys = () => {
        dispatch(fetchApiKeysCount());
        dispatch(fetchApiKeys({
            page: page ? page : 1,
            perPage: perPage
        }));
    };

    if(!fetchedSuccess){
        return <Spinner/>;
    }

    return (
        <Layout>
            <ActionsBar
                title={useTranslation("APIs dashboard panel")}
                actions={<GenerateApiKeys count={fetchedCount} refreshApiKeys={handleRefreshApiKeys} />}
            />
            <main>
                <Breadcrumbs crumbs={[
                    {
                        label: useTranslation("Registered Custom Post Types"),
                        link: "/"
                    },
                    {
                        label: useTranslation("ACPT Tools"),
                        link: "/tools"
                    },
                    {
                        label: useTranslation("APIs"),
                    }
                ]} />
                <div>
                    <ApiKeysList refreshApiKeys={handleRefreshApiKeys} count={fetchedCount} elements={fetched} page={page} perPage={perPage} />
                </div>
                <div className="acpt-card">
                    <div className="acpt-card__header">
                        <div className="acpt-card__inner">
                            <h3>{useTranslation("APIs endpoints")}</h3>
                        </div>
                    </div>
                    <div className="acpt-card__body">
                        <SwaggerUI url="/?rest_route=/acpt/v1/schema" />
                    </div>
                </div>
            </main>
        </Layout>
    );
};

export default ApiDashboard;