import {wpAjaxRequest} from "../../utils/ajax";
import {generateApiKeyFailure, generateApiKeyInProgress, generateApiKeySuccess} from "../actions/generateApiKeyActions";

export const generateApiKey = () => async ( dispatch, getState) => {
    try {
        dispatch(generateApiKeyInProgress());
        const res = await wpAjaxRequest('generateApiKeyAction', {} );
        if (res.success === true)  {
            dispatch(generateApiKeySuccess(res.data));
        } else {
            dispatch(generateApiKeyFailure(res.error)) ;
        }
    } catch ( e ) {
        dispatch(generateApiKeyFailure(e));
    }
};